.counter {
    margin-top: 28px;

    &__title {
        @include page-title-one;
        font-family: 'Raleway';
        line-height: 110%;
        color: $black-color;
        margin-bottom: 50px;
    }
    &__block {
        display: flex;
        column-gap: 40px;
    }
    &__pokemon {
        background-color: #F8F8F8;
        box-shadow: 0 5px 7px 0 rgba(0, 0, 0, 0.25);
        width: 558px;
        height: 565px;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    &__pokemon-img {
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
    }
    &__pokemon-number {
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 25px;
        font-size: 48px;
        font-weight: 400;
        color: $black-color;
        text-transform: uppercase;
        font-family: 'Alatsi';
        margin-bottom: 28px;
    }
    &__edit {
        width: 31px;
        height: 31px;
        display: inline-block;
        background-image: url("../img/icons/pen.png");
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        right: 30%;
        cursor: pointer;
        transition: $transition;

        &:hover {
            background-image: url("../img/icons/pen-pink.png");
        }
    }
    &__control {
        display: flex;
        justify-content: center;
        column-gap: 25px;
    }
    &__plus {
        background-color: #242424;
        border: 3px solid transparent;
        width: 60px;
        height: 60px;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: start;
        cursor: pointer;
        transition: $transition;

        &::before {
            content: "+";
            font-weight: 400;
            font-size: 48px;
            line-height: 115%;
            text-transform: uppercase;
            font-family: 'Alatsi';
            color: #fff;
        }

        &:hover {
            border-color: $pink-color;
            background-color: #fff;

            &::before {
                color: $pink-color;
            }
        }
    }
    &__minus {
        background-color: #242424;
        border: 3px solid transparent;
        width: 60px;
        height: 60px;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: start;
        cursor: pointer;
        transition: $transition;

        &::before {
            content: "-";
            font-weight: 400;
            font-size: 48px;
            line-height: 115%;
            text-transform: uppercase;
            font-family: 'Alatsi';
            color: #fff;
            transition: $transition;
        }

        &:hover {
            border-color: $pink-color;
            background-color: #fff;

            &::before {
                color: $pink-color;
            }
        }
    }
    &__data {
        display: flex;
        flex-direction: column;
        &-list {
            display: flex;
            column-gap: 40px;
            margin-bottom: 20px;
        }
        &-names {
            display: flex;
            flex-direction: column;
            row-gap: 10px;
        }
        &-values {
            display: flex;
            flex-direction: column;
            row-gap: 10px;
        }
        &-name {
            font-weight: 700;
            font-size: 20px;
            height: 25px;
            color: $black-color;
        }
        &-value {
            font-size: 20px;
            color: $black-color;
            position: relative;

            .counter__edit {
                left: 15%;
                width:20px;
                height: 20px;
                top: 0;
            }
        }
        &-charm {
            position: relative;
            &::before {
                content: "";
                display: block;
                width: 25px;
                height: 25px;
                border: 2px solid $black-color;
                border-radius: 5px;
            }
            &::after {
                content: "";
                display: block;
                width: 36px;
                height: 30px;
                position: absolute;
                top: -5px;
                left: 2px;
                background-image: url("../img/icons/pink-galochka.png");
            }
        }

    }
    &__timer {
        margin-bottom: auto;

        &-info {
            font-size: 32px;
            color: $black-color;
            font-weight: 700;
            margin-bottom: 25px;

            & span {
                margin-right: 10px;
            }
        }

        &-controls {
            display: flex;
            column-gap: 25px;
        }
        &-play {
            width: 60px;
            height: 60px;
            background-color: #242424;
            border-radius: 10px;
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
            transition: $transition;

            &:hover {
                background-color: $pink-color;
            }
        }
        &-reset {
            width: 60px;
            height: 60px;
            background-color: #242424;
            border-radius: 10px;
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
            transition: $transition;
            &:hover {
                background-color: $pink-color;
            }
        }
    }
    &__cur-time {
        font-family: 'Alatsi';
        font-weight: 400;
        font-size: 32px;
        color: $black-color;
    }
    &__chance {
        &-name {
            font-weight: 700;
            color: $black-color;
            font-size: 34px;
            letter-spacing: -0.01em;
            text-transform: uppercase;
            margin-right: 30px;
        }
        &-number {
            color: $black-color;
            font-family: 'Alatsi';
            font-size: 34px;
            font-weight: 400;
        }
    }
    &__buttons {
        margin-top: 30px;
        display: flex;
        column-gap: 26px;
    }
    &__edit-btn {
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        padding: 20px 13px;
        background-color: $black-color;
        display: flex;
        align-items: center;
        cursor: pointer;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        transition: $transition;
        border: 3px solid transparent;

        &:hover {
            background-color: #fff;
            color: $black-color;
            border-color: $black-color;
        }
    }
    &__shiny-btn {
        font-weight: 700;
        font-size: 24px;
        color: #fff;
        text-transform: uppercase;
        background-color: $black-color;
        display: flex;
        align-items: center;
        column-gap: 10px;
        padding: 11px;
        width: 180px;
        justify-content: space-around;
        cursor: pointer;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        transition: $transition;
        border: 3px solid transparent;

        &::after {
            content: "";
            display: block;
            width: 30px;
            height: 30px;
            background-image: url("../img/icons/white-sparkles.png");
        }

        &:hover {
            background-color: #fff;
            border-color: $pink-color;
            color: $pink-color;

            &::after {
                background-image: url("../img/icons/pink-sparkles.png");
                animation: scale-animation 2s alternate infinite;
            }
        }
    }
    &__delete-btn {
        width: 60px;
        background-color: $black-color;
        background-image: url("../img/icons/trash.png");
        background-repeat: no-repeat;
        background-position: center;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
        background-size: 65%;
        transition: $transition;

        &:hover {
            background-color: $dark-grey;
            background-image: url("../img/icons/trash-black.png");
        }
    }
    &__settings {
        margin-top: 95px;

        &-title {
            margin: 0;
            @include page-title-two;
            color: $black-color;
            text-transform: uppercase;
            margin-bottom: 55px;
        }
        &-btn {
            background-color: #d18abd;
            margin: 0 16px 20px;
            padding: 15px;
            font-size: 17px;
            font-weight: 500;
            color: #fff;
            text-transform: uppercase;
            border: none;
            box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
            border: 2px solid transparent;

            &.disabled {
                background-color: grey;
                cursor: none;

                &:hover {
                    border-color: transparent;
                    color: #fff;
                }
            }
        }
    }
    &__settings-tab {
        padding: 16px 0;
        padding-left: 24px;
        border-bottom: 1px solid #bfbfbf;
        border-right: 5px solid #bfbfbf;
        text-transform: uppercase;
        font-weight: 500;
        cursor: pointer;
        transition: $transition;

        &.active {
            background-color: #fff;
            border-right: 5px solid #5b5897;
            box-shadow: 0 2px 5px 0 rgb(75 75 75 / 25%);
        }

        &:hover {
            border-right: 5px solid #5b5897;
            box-shadow: 0 2px 5px 0 rgb(75 75 75 / 25%);
        }
    }
    &__settings-tabs {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-width: 322px;
        border-right: 1px solid #e0e0e0;
    }
    &__settings-body {
        width: 100%;
        padding: 15px 30px 30px 30px;
    }
    &__settings-block {
        min-height: 435px;
        background-color: #F8F8F8;
        box-shadow: 3px 0 18px 0 rgba(0, 0, 0, 0.25);
        display: flex;
    }
    &__settings-pokemons {
        display: none;
        grid-template-columns: repeat(4, 1fr);
        gap: 15px;

        &.active {
            display: grid;
        }
    }
    &__search-pokemon {
        display: flex;
        justify-content: space-between;

        form {
            position: relative;
        }
    }
    &__search-icon {
        width: 30px;
        height: 30px;
        background-image: url("../img/icons/search-icon-purple.png");
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        top: 5px;
        right: 15px;
        cursor: pointer;
    }
    &__setting-next {
        height: 45px;
        outline: none;
        border: none;
        padding: 0 30px;
        background-color: #7b78a8;
        color: #fff;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 500;
        @include font-roboto;
    }
    &__input-search {
        border: none;
        border-bottom: 3px solid #bfbfbf;
        margin-bottom: 25px;
        width: 400px;
        height: 40px;
        background-color: transparent;
        padding: 10px 42px 10px 20px;
        outline: none;
        transition: $transition;

        &::placeholder {
            text-transform: uppercase;
        }
        &:focus,
        &:hover {
            border-color: $purple-color;
        }
    }
    &__games {
        display: none;

        &.active {
            display: grid;
        }
    }
    &__methods {
        display: none;

        &.active {
            display: grid;
        }
    }
    &__other-settings {
        display: none;
        &.active {
            display: grid;
        }
    }
    &__pokemon-item {
        display: flex;
        column-gap: 5px;
        justify-content: space-between;
        background-color: #fff;
        padding: 10px;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
        transition: $transition;
        border: 1px solid transparent;

        &:hover {
            box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.25);
        }

        &.active {
            border-color: #7b78a891;
            box-shadow: 0 6px 8px 0 #7b78a875;
        }
    }
    &__pokemon-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__pokemon-name {
        text-transform: uppercase;
        font-size: 16px;
        line-height: 117%;
        @include font-roboto;
        color: $black-color;
    }
    &__pokemon-rang {
        @include font-alatsi;
        color: $black-color;
    }
    &__games {
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;
    }
    &__game {
        background-color: #fff;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        column-gap: 5px;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
    }
    &__game-img img {
        border-radius: 5px;
    }
    &__game-name {
        font-size: 15px;
    }
    &__methods {
        grid-template-columns: repeat(4, 1fr);
        gap: 10px;
    }

    &__method {
        background-color: #fff;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
        text-align: center;
        padding: 15px;
    }
    &__other-settings {
        grid-template-columns: repeat(3, 1fr);
        gap: 15px;
    }
    &__other-setting {
        display: flex;
        background-color: #fff;
        padding: 15px 20px;
        justify-content: space-between;
        column-gap: 5px;
        align-items: center;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
    }
    &__other-setting-name {
        text-transform: uppercase;
    }
    &__other-setting-icon {
        background-repeat: no-repeat;
        background-size: contain;
        width: 40px;
        height: 40px;
    }
    &__add {
        margin-top: 95px;
        padding: 45px;
        padding-bottom: 75px;
        background-color: $grey-color;
        box-shadow: 0 5px 7px 0 rgba(0, 0, 0, 0.25);

        &-title {
            text-align: center;
            text-transform: uppercase;
            font-weight: 400;
            font-size: 32px;
            color: #838383;
            margin-bottom: 55px;
        }
        &-btn {
            border-radius: 25px;
            width: 240px;
            height: 99px;
            border: 5px solid #5f5f5f;
            background-image: url("../img/icons/plus.png");
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
            margin: 0 auto;
        }
            
        
    }
    &__info {
        margin-top: 95px;
        margin-bottom: 80px;

        &-title {
            @include page-title-two;
            text-transform: uppercase;
            margin-bottom: 50px;
            color: $black-color;
        }
        &-descr {
            color: #535353;
            @include main-page-text;
        }
    }
}
.count-hunts {
    margin-top: 95px;

    &__title {
        @include page-title-two;
        text-transform: uppercase;
        color: $black-color;
        margin-bottom: 50px;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
        margin-bottom: 60px;
    }
    &__item {
        background-color: $grey-color;
        box-shadow: 0 5px 7px 0 rgba(0, 0, 0, 0.25);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 25px;
    }
    &__item-img {
        width: 250px;
        height: 280px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        margin-bottom: 30px;
    }
    &__item-name {
        text-transform: uppercase;
        font-weight: 400;
        color: $black-color;
        font-size: 24px;
        margin-bottom: 10px;
    }
    &__item-number {
        @include font-alatsi;
        font-size: 36px;
        text-transform: uppercase;
        color: $black-color;
    }
    &__button {
        font-size: 24px;
        color: #fff;
        text-transform: uppercase;
        font-weight: 500;
        background-color: $black-color;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        padding: 20px 30px;
        margin: 0 auto;
        cursor: pointer;
    }
    &__all {
        display: flex;
    }
}
