.breadcrumbs {
    margin-top: 28px;
    display: flex;

    span {
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 700;
        margin-right: 5px;
        color: #616161;

        a {
            color: #474747;
            display: flex;
            align-items: center;
            column-gap: 5px;

            &::after {
                content: "";
                width: 15px;
                height: 11px;
                display: block;
                background-image: url("../img/icons/breadcrumb.png");
                background-size: contain;
                background-repeat: no-repeat;
            }
        }
    }
}