.filter-tabs {
    display: flex;
    column-gap: 10px;
    margin-top: 40px;
    margin-bottom: 40px;
}
.filter-tab {
    padding: 10px 30px;
    font-size: 14px;
    line-height: 110%;
    text-transform: uppercase;
    background-color: $dark-grey;
    color: $black-color;
    transition: $transition;
    min-width: 140px;
    text-align: center;
    cursor: pointer;
    border: 2px solid transparent;

    &.active {
        background-color: $black-color;
        color: #fff;
    }

    &:hover {
        background-color: #fff;
        border-color: $black-color;
        color: $black-color;
    }
}