.about-shiny {
    background-color: $black-color;

    .container {
        padding: 65px 60px;
    }

    &__title {
        @include page-title-two;
        @include font-roboto;
        text-transform: uppercase;
        color: #fff;
        margin: 0;
        margin-bottom: 45px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }
    &__text {
        @include main-page-text;
        @include font-roboto;
        color: rgba(255, 255, 255, 0.83);
        max-width: 650px;
    }
    &__image {
        min-width: 325px;
    }
}