@import 'mixins.scss';
@import 'vars.scss';

* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    @include font-roboto;
    color: $black-color;
    box-sizing: border-box;
    background-color: #fff;
}

.container {
    width: 1148px;
    margin: 0 auto;
}

p {
    margin: 0;
    margin-bottom: 15px;
}
  
img {
    display: block;
    max-width: 100%;
    max-height: 100%;
}

ul,
ol,
li {
    list-style: none;
}
ul,
ol {
    padding-left: 0;
    margin-bottom: 15px;
}
li {
    padding-left: 30px;
    position: relative;
    margin-bottom: 10px;
}
.content li::before {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-color: $pink-color;
    transform: rotate(45deg);
    position: absolute;
    left: 7px;
    top: 5px;
}
a {
    color: $pink-color;
    text-decoration: none;
}
.btn {
    padding: 10px 15px;
    margin-bottom: 15px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    transition: $transition;
    cursor: pointer;
}
.btn-black {
    background-color: $black-color;
    border: 2px solid transparent;
    color: #fff;

    &:hover {
        background-color: #fff;
        border-color: $black-color;
        color: $black-color;
    }
}
.btn-pink {
    background-color: $pink-color;
    border: 2px solid transparent;
    color: #fff;

    &:hover {
        background-color: #fff;
        border-color: $pink-color;
        color: $pink-color;
    }
} 
.load-more {
    display: flex;
    justify-content: center;
    align-items: center;
}
.load-more-btn {
    padding: 15px 45px;
    font-size: 24px;
    font-weight: 300;
    text-transform: uppercase;
    transition: $transition;
}
@media(max-width:1160px){
    .container {
        width: 100%;
    }
}
@keyframes scale-animation {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(1.3);
    }
}