.useful-info {
    background-color: $grey-color;
    padding-top: 50px;
    padding-bottom: 150px;

    &__title {
        margin: 0;
        margin-bottom: 30px;
        font-size: 38px;
        font-weight: 600;
        color: $black-color;
        text-transform: uppercase;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        column-gap: 15px;
    }
    &__content {
        max-width: 725px;
    }
    &__text {
        font-weight: 400;
        font-size: 21px;
        line-height: 115%;
        color:#535353;
        margin-bottom: 80px;
    }
    &__btn {
        display: inline-block;
        color: #fff;
        font-size: 24px;
        line-height: 115%;
        font-weight: 500;
        padding: 17px 40px;
        text-transform: uppercase;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
    }
    &__img {
        max-height: 320px;
    }
}