.error-404 {
    display: flex;
    padding-top: 40px;
    padding-bottom: 120px;
    align-items: center;
    justify-content: space-evenly;
    column-gap: 20px;

    &__text {
        font-size: 28px;
        text-transform: uppercase;
        text-align: center;
        font-weight: 300;
        margin-top: 45px;
        margin-bottom: 50px;
    }
    &__number {
        display: flex;
        justify-content: center;
    }
    &__btn {
        text-align: center;
    }
    &__link {
        color: #fff;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 300;
        padding: 15px 45px;
        background-color: $black-color;
    }
}