.search {
    padding-bottom: 100px;
    &__title {
        @include page-title-one;
    }
    &__input {
        input[type=text] {
            width: 100%;
            height: 45px;
            font-size: 22px;
            border: none;
            padding: 5px;
            padding-left: 10px;
            outline: none;
            background-color: transparent;
            border-bottom: 1px solid $black-color;

            &::placeholder {
                text-transform: uppercase;
                color: #535353;
                font-size: 22px;
            }
            &:focus,
            &:hover {
                border-color: $pink-color;
            }
        }
    }
    &__form {
        position: relative;
        input[type=submit] {
            display: none;
        }
    }
    &__btn-icon {
        width: 35px;
        height: 35px;
        background-image: url("../img/icons/search-icon.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transition: $transition;
        position: absolute;
        right: 0;
        top: 5px;
        cursor: pointer;

        &:hover,
        &:active{
            background-image: url("../img/icons/search-icon-hover.png");
        }
    }

    &__item {
        display: block;
        
        color: $black-color;
        min-height: 180px;
        padding: 30px;
        transition: $transition;
        border-top: 1px solid $black-color;

        &:last-child {
            border-bottom: 1px solid $black-color;
        }

        &-top {
            display: flex;
            justify-content: space-between;
            margin-bottom: 25px;
        }
        &-bottom {
            display: flex;
            column-gap: 15px;
        }
        &-title {
            border-bottom: 1px solid $black-color;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 28px;
        }

        &-text {
            font-size: 20px;
            line-height: 117%;
            font-weight: 300;
        }
        &-date {
            @include font-alatsi;
        }

        &:hover {
            border-color: $pink-color;

            &+ .search__item {
                border-top:1px solid $pink-color;
            }
        }
    }
    & .load-more {
        margin-top: 55px;
    }
}