.articles {
    padding-bottom: 75px;

    &__title {
        margin-top: 20px;
        margin-bottom: 50px;
        @include page-title-one;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;

        &--news {
            .article:first-child {
                grid-column: 1/3;
            }
        }
    }

    .load-more {
        grid-column: 1/5;
        color: #fff;
        font-size: 24px;

        &-btn {
            margin: 20px 0;
        }
    }
}
.article {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
    transition: $transition;
    background-color: $grey-color;
    cursor: pointer;

    &__img {
        width: 100%;

        img {
            object-fit: cover;
            width: 100%;
            max-height: 160px;
        }
    }
    &__wrapper {
        padding: 16px;
    }
    &__title {
        color: $black-color;
        font-size: 16px;
        line-height: 120%;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 10px;
    }
    &__description {
        color: #959595;
        font-weight: 400;
        font-size: 12px;
        display: -webkit-box;
        -webkit-line-clamp:2;
        margin-bottom: 20px;
    }
    &__date {
        color: $black-color;
        font-size: 12px;
        font-weight: 400;
        text-align: end;
    }
    &:hover {   
        box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.25);
    }
}