.other-posts {
    background-color: $grey-color;
    padding-top: 50px;
    padding-bottom: 150px;

    &__title {
        margin: 0;
        margin-bottom: 30px;
        font-size: 38px;
        font-weight: 600;
        color: $black-color;
        text-transform: uppercase;
    }

    &__list {
        display: flex;
        justify-content: space-between;
        column-gap: 20px;
    }
}