.about-us {
    background-color: #F8F8F8;

    &__wrapper {
        padding: 65px 60px;
        display: flex;
        column-gap: 90px;
    }

    &__title {
        @include font-roboto;
        @include page-title-two;
        color: $black-color;
        text-shadow: $text-shadow;
        margin: 0;
        margin-bottom: 45px;

        span {
            color: #d18abd;
        }
    }

    &__info {
        max-width: 650px;
    }

    &__text {
        @include main-page-text;
        @include font-roboto;
        color: $black-color;
    }
}