.article-detail {
    padding-bottom: 80px;

    &__title {
        @include page-title-one;
    }
    &__date {
        font-weight: 600;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    &__banner {
        img {
            max-height: 500px;
            width: 100%;
            object-fit: cover;
        }
    }
}